import { StoreProductItem, ProductItem } from 'store/constants/products';

export const normalizeProductItem = (productItem: ProductItem): StoreProductItem => ({
    name: productItem.name,
    id: productItem.id,
    price: Number(productItem.pricePerUnit.value),
    quantityLeft: Number(productItem.quantity),
    imageUrl: productItem.imageUrl[0] || '',
    description: productItem.description,
    brand: productItem.brand,
    manufacturer: productItem.manufacturer,
    country: productItem.country,
    volume: productItem.volume,
    weight: productItem.weight,
    size: productItem.size,
    ingredients: productItem.ingredients,
    energy: productItem.energy,
    protein: productItem.protein,
    fat: productItem.fat,
    carbohydrate: productItem.carbohydrate,
    storageConditions: productItem.storageConditions,
});
