import { AppReducer } from 'store/constants';

const initialSubcategoryIndex = 2;

export const subcategoriesModel: AppReducer = (state, action) => {
    const { subcategoriesData, subcategoriesPage } = state;

    switch (action.type) {
        case 'SHOW_SUBCATEGORIES': {
            const initialSubcategoryId =
                Object.values(subcategoriesData.itemsMap)[initialSubcategoryIndex]?.category.id || '';

            subcategoriesPage.activeItemId = initialSubcategoryId;
            break;
        }
        case 'subcategories/SWITCH_ACTIVE_ITEM': {
            subcategoriesPage.activeItemId = action.payload;
            break;
        }
        default:
            break;
    }
};
