import { AppReducer } from 'store/constants';
import { StoreCategory } from 'store/constants/categories';
import { Subcategory, StoreSubcategory, SubcategoryCategory } from 'store/constants/subcategories';
import { normalizeProductItem } from 'store/reducers/helpers/productItem';

const normalizeSubcategoryCategory = (category: SubcategoryCategory): StoreCategory => ({
    id: category.id,
    rank: category.rank,
    name: category.name || '',
    imageUrl: category.imageUrl?.[0] || '',
});

const normalizeSubcategory = (subcategory: Subcategory): StoreSubcategory => ({
    category: normalizeSubcategoryCategory(subcategory.category),
    products: subcategory.products.map(normalizeProductItem),
});

export const normalizeSubcategories = (subcategories: Subcategory[]): Record<string, StoreSubcategory> =>
    subcategories.reduce<Record<string, StoreSubcategory>>((acc, subcategory) => {
        acc[subcategory.category.id] = normalizeSubcategory(subcategory);

        return acc;
    }, {});

export const subcategoriesData: AppReducer = (state, action) => {
    switch (action.type) {
        case 'SHOW_SUBCATEGORIES': {
            state.subcategoriesData.itemsMap = normalizeSubcategories(action.payload.subcategories);
            break;
        }

        default:
            break;
    }
};
