import { useSelector } from 'hooks/useSelector';
import React from 'react';
import styled from 'styled-components';

const StyledRoot = styled.div`
    padding-top: 24px;
`;

const StyledSubCategoryListWrapper = styled.div`
    height: 506px;
    overflow: hidden;
    grid-column: 1;
`;
const StyledSubCategoryListScrollableWrapper = styled.div`
    display: block;
    overflow-x: scroll;
    padding-left: 112px;
    padding-bottom: 50px;
    scroll-snap-type: x mandatory; // нужно для примагничивания
    overscroll-behavior-x: auto;
`;
const StyledSubCategoryListContent = styled.div`
    display: grid;
    grid-template-columns: repeat(auto-fill, 330px);
    grid-auto-flow: row;
    grid-gap: 32px;
    box-sizing: border-box;
    padding-right: 112px;

    height: 506px;
    width: max-content;
    min-width: 100%;
`;
const StyledSubCategoryListItem = styled.div`
    position: relative;
    height: 506px;
    width: 330px;
    background: #ccc;
    border-radius: 36px;
    scroll-snap-align: center; // нужно для примагничивания

    grid-row: 1;
`;

interface SubCategoryListProps {
    id: string;
}
export const SubCategoryList: React.FC<SubCategoryListProps> = ({ id }) => {
    const products = useSelector(({ subcategoriesData }) => subcategoriesData.itemsMap[id].products);
    const items = products.map(({ id }) => {
        return <StyledSubCategoryListItem key={id} />;
    });

    return (
        <StyledSubCategoryListWrapper>
            <StyledSubCategoryListScrollableWrapper>
                <StyledSubCategoryListContent>{items}</StyledSubCategoryListContent>
            </StyledSubCategoryListScrollableWrapper>
        </StyledSubCategoryListWrapper>
    );
};

const StyledCarouselListWrapper = styled.div`
    overflow: hidden;
`;
const StyledCarouselListScrollableWrapper = styled.div`
    display: block;
    overflow-y: scroll;
    overflow-x: hidden;
    padding-right: 50px;
    width: 110%;
`;
const StyledCarouselListContent = styled.div`
    display: grid;
    grid-template-rows: repeat(auto-fill, 506px);
    grid-auto-flow: column;
    grid-gap: 32px;
    box-sizing: border-box;
    //padding-right: 112px;

    height: 506px;
    width: max-content;
    min-width: 100%;
`;
const StyledCarousel = styled.div`
    background: #ccc;

    height: 506px;
    width: 3000px;
`;

export const SubCategoriesPortal = () => {
    const subcategoriesMap = useSelector(({ subcategoriesData }) => subcategoriesData.itemsMap);
    const carousels = React.useMemo(() => {
        // const ids = Object.keys(subcategoriesMap);
        const ids = [Object.keys(subcategoriesMap)[0]].filter(Boolean);

        return ids.map((subCategoryId) => {
            // return <StyledCarousel key={subCategoryId} id={subCategoryId} />;
            return <SubCategoryList key={subCategoryId} id={subCategoryId} />;
        });
    }, [subcategoriesMap]);

    if (!carousels.length) {
        return null;
    }

    return <StyledRoot>{carousels}</StyledRoot>;

    // return (
    //     <StyledRoot>
    //         <StyledCarouselListWrapper>
    //             <StyledCarouselListScrollableWrapper>
    //                 <StyledCarouselListContent>{carousels}</StyledCarouselListContent>
    //             </StyledCarouselListScrollableWrapper>
    //         </StyledCarouselListWrapper>
    //     </StyledRoot>
    // );
};
