import { AppThunkAction } from 'store/constants';
import { ROUTES } from 'store/constants/router';
import { push } from 'connected-react-router';
import { askForCategoryByIdAction } from 'store/actions/server';

export const categoriesItemSelectThunk = (payload: string): AppThunkAction => (dispatch) => {
    dispatch(askForCategoryByIdAction(payload));
    dispatch(push(ROUTES.SUBCATEGORIES));
};
